:root{
    --light: rgb(238, 236, 236);
    --primary: purple;
    --secondary:  #db1356;
    --dark: rgb(19, 18, 18);
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: cursive;
}

.quote-container{
    text-align: center;
    min-height: 100vh; 
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3rem 0.5rem;
}

#loading-img{
    width: 12rem;
}

#quote{
    position: relative;
    width: 80%;
    margin: 0 auto 3rem;
}

#quote-character, #quote-anime{
    position: absolute;
    padding: 1.2rem;
    border-radius: 2rem;
    font-size: 1.1rem;
    color: #fff;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

#quote-character{
    background-color: var(--primary);
}

#quote-quote{
    background-color: var(--dark);
    color: #fff;
    margin: auto;
    line-height: 1.5;
    padding: 3rem 0.5rem;
    border-radius: 2rem;
    font-size: 1.3rem;
}

.clamp{
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow:hidden;
    text-overflow: ellipsis;
}

#quote-anime{
    background-color: var(--secondary);
}

.btn {
    border: none;
    outline: none;
    color: #fff;
    text-transform: capitalize;
    background-color: var(--primary);
    padding: 1rem;
    border-radius: 2rem;
    font-size: 1.1rem;
}

.collapse-btn{
    margin-top: 0.5rem;
    font-size: 0.8rem;
    padding: 0.7rem;
}

/* ----------------------------- media */
/* ----------------------------- 1 */
@media screen and (max-width:576px){
    html{
      font-size: 13px;
    }

    #quote-character,  #quote-anime{
        transform: translateY(-50%);
        min-width: 15%;
        max-width: 220px;
    }

    #quote-character{
        top: 0;
        left: -10%;
    }
    
    #quote-anime{
        top: 100%;
        right: -10%;
    }
  }
  
  /* ----------------------------- 2 */
  @media screen and (min-width:576px) and (max-width:769px){
    html{
      font-size: 15px;
    }

    #quote-character,  #quote-anime{
        transform: translateY(-50%);
        min-width: 118px;
        max-width: 250px;
    }
     
    #quote-character{
        top: 0;
        left: -60px;
    }
    
    #quote-anime{
        top: 100%;
        right: -60px;
    }
  }
  /* ----------------------------- 3 */
  @media screen and (min-width:769px){
    html{
      font-size: 16px;
    }

    #quote-character,  #quote-anime{
        transform: translateY(-50%);
        min-width: 156px;
        max-width: 300px;
    }
     
    #quote-character{
        top: 0;
        left: -80px;
    }
    
    #quote-anime{
        top: 100%;
        right: -80px;
    }
  }